import React, { useContext, useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import Title from '../Title/Title';
import AboutImg from '../Image/AboutImg';
import PortfolioContext from '../../context/context';

const About = () => {
  const { about } = useContext(PortfolioContext);
  const { img, paragraphOne, paragraphTwo, paragraphThree, resume } = about;

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="about">
      <Container>
        <Title title="A propos de moi" />
        <Row className="about-wrapper">
          <Col md={6} sm={12}>
            <Fade bottom duration={1000} delay={600} distance="30px">
              <div className="about-wrapper__image">
                <AboutImg alt="profile picture" filename={img} />
              </div>
            </Fade>
          </Col>
          <Col md={6} sm={12}>
            <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={1000} distance="30px">
              <div className="about-wrapper__info">
                <p className="about-wrapper__info-text">
                  {
                    "C'est un peu par pur hasard que je suis tomb dans l'IT quand j'tais plus jeune. \
                    Au dpart, je souhaitais crer un jeu vido car je suis fan de cet univers. \
                    Et je ne pensais pas qu'on pouvait faire d'un hobby son mtier."
                  }
                </p>
                <p className="about-wrapper__info-text">
                  {
                    "Du coup, aprs 3 ans  la HEPL (INPRES), je dcide de me lancer dans ce secteur d'activit \
                    afin d'aider toutes les personnes que je rencontre  simplifier leur vie en crant des outils capables de les aider."
                  }
                </p>
                <p className="about-wrapper__info-text">
                  {'Des sujets qui me tiennent  coeur : Open Source, Cloud et Green IT.'}
                </p>
                {
                  <span className="d-flex mt-3">
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      className="cta-btn cta-btn--resume"
                      href="https://bit.ly/cvelbazz"
                    >
                      Carrire (CV)
                    </a>
                  </span>
                }
              </div>
            </Fade>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default About;
